:root {
  --background-color: #252525 ;
  --box-shadow:  5px 5px 10px #171717,
  -5px -5px 10px #333333;
  --white: #e7e7f7;
}

html {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "system";
  width: 100%;
  height: 100%;
}
.wrapper {
  background: var(--background-color);
  height: 100vh;
  width: 100%;
  display: grid;
  place-items: center;
}
.header {
    position: absolute;
    left: 0;
    top: 0;
    display: grid;
    place-items: center;
    text-align: center;
    height: 70px;
    width: 100vw;
    background: var(--background-color);
    box-shadow: var(--box-shadow);
    font-size: 30px;
    letter-spacing: 1.5px;
    font-weight: 600;
    color: var(--white);
}
.main-div .text {
  margin-left: 30px;
}
@media only screen and (max-width:350px) {
  .main-div2 span:nth-child(3) {
    font-size: 25px;
  }

}

.main-div {
  display: flex;
  justify-content: center;
}

input[type="text"] {
  border-radius: 3px;
  background: var(--background-color);
  box-shadow: var(--box-shadow);
  color: var(--white);
  width: 28%;
  outline: none;
  border: 2px solid #353535;
  margin: 10px;
  padding: 9px 10px;
  text-transform: uppercase;
  caret-color:white;
  font-weight: 700;
  letter-spacing: 1px;
}

button {
  padding: 9px 10px;
  margin: 10px;
  width: 12%;
  outline: none;
  border: none;
  color: var(--white);
  border-radius: 3px;
  background: var(--background-color);
  font-weight: 700;
  box-shadow: var(--box-shadow);
  letter-spacing: 1px;
}
button:active {
  box-shadow: 1px 1px 5px #3b3535;
  transform: translateY(4px);
}


.main-div2 {
  max-height: 100%;
  max-width: 100%;
  width: 80%;
  height: 90vh;
  display: block;
  text-align: center;
  height: 80%;
  border-radius: 13px;
  background: var(--background-color);
  box-shadow: var(--box-shadow);
  padding: 30px 20px 30px 30px;
  margin: 15px;
  /* margin-top: 50px;
  line-height: 1.5;
  font-weight: 600; */
  text-align: left;
  font-size: 25px;
  color: var(--white);
  font-family: "Handvetica";
  /* text-shadow: -8px -8px 12px rgba(255,255,255,0.4),
                8px 8px 12px rgba(0,0,0,0.08); */
  caret-color:#262626;
  outline: none;
}
.main-div2 p {
  font-size: 21px;
  color: antiquewhite;
  text-align: right;
 
}
.main-div2 span:nth-child(1) {
  font-size: 30px;
  letter-spacing: 1.5px;
  padding:0;
}
.main-div2 span:nth-child(3) {
  font-size: 45px;
  padding-left: 30px;
  letter-spacing: 1.5px;
  font-weight: 700;
}
.main-div2 img {
  width: 100px;
  object-fit: contain;
  margin-left: 20%;
  margin-bottom: -38px;
  transition: 0.5s;
}
.wind {
  margin-left: 100px;
  letter-spacing: 1.5px;
  font-size: 38px;
}